import { Document, Page, View, Text, StyleSheet, Link, Image} from '@react-pdf/renderer';
import React, { useEffect, useState } from 'react';

//CONST
import experienceData from './cv/Data/DataExperience';
import certificationsData from './cv/Data/DataCertifications';
import educationData from './cv/Data/DataEducation';
import baselineData from './cv/Data/DataBaseline';

import {CSVurl} from "../config"


import  { csv } from 'd3';

import imageData from './cv/Data/DataPicture'


// Create styles
const styles = StyleSheet.create({
    page: {
      flexDirection: 'row',
      backgroundColor: '#E4E4E4',
      fontSize: '11px',
    },
    section: {
        marginLeft: '25px',
        flexGrow: 1,

      
    },

    footer: {
        
        flexDirection: 'row',
        flexGrow: 1,
        marginTop: '0px',
        justifyContent: "space-around",
        alignItems: 'center',
    },

    link:{
        fontSize: '10px',
        textDecoration:'none',
        color: '#000000',
        margin: 2,
    },

    skills:{
        marginTop: '20px',
        marginLeft: '25px',
        flexGrow: 1,
        paddingBottom: '10px',
    },

    row: {
        flexGrow: 1,
        flexDirection: 'row',
        alignItems: 'center',
        paddingTop: '20px',
      },

    infos : {
        width:'20%',
        textAlign: 'justify',
        alignContent:'center',
        marginLeft: '5px',
        textDecoration: 'none',
        alignSelf:'center',
        
    },
    imageContent: {
        width: '20%',
        borderRadius: '50%',

      },

    baseline: {
        width : '60%',
        paddingRight : '35px',
        textAlign: 'justify',
        alignSelf:'center',
  
    },

    contentSection:{
        marginBottom: "10px",
        width: 480,
        marginLeft: 10,
    },


    display:{
        flexGrow: 2,
        flexDirection: 'row',
        alignItems: 'center',
        paddingTop: '20px',
    },
    
    titleSection:{
        marginBottom: '10px',

    },

    underlineTitle: {
        marginBottom: 15,
        borderBottomWidth: 1,
        borderBottomColor: '#112131',
        borderBottomStyle: 'solid',
        width: 500,
    }, 
    details:{
        marginLeft:5,
        color: '#a9a9a9',
    },

    detailsBaseline: {
        marginLeft:5,
        color: '#a9a9a9',
        width: 250,

    }
  });

  
const MyDoc = () => {

    const [data, setData] = useState([]);
    useEffect(()=> {
        csv(CSVurl).then(data=>{
        setData(data)
        });
    
    }, []);

    const LanguageFilter = data.filter(x => x.TechnoType === "Language" && x.Open_Skill === "Yes")
    const FrameworkFilter = data.filter(y => y.TechnoType === ("Framework" || "Engine" || "Library" || "Tool") && y.Open_Skill === "Yes")




    return (    
        <Document>
        <Page size="A4" style={styles.page}>
        <View>
            <View style={styles.section}> 

                <View style={styles.row}>

                    <Image style={styles.imageContent} src={imageData[0]}/>
                    
                    <View style={styles.infos}>
                        <Text>Léo Gravelin</Text>
                        <Text>31 ans</Text>
                    </View> 
                
                    <View style={styles.baseline}>
                        <Text style={styles.titleSection}>{baselineData[0]}</Text>
                        <Text style={styles.detailsBaseline}>{baselineData[1]}</Text>
                    </View>
            
                   

                    
                    
                </View>
             

            </View>

            {/* SKILLS TO SPREAD MY LANGUAGES AND ENGINE */}
            <View style={styles.skills}>

                <View style={styles.contentSection}>
                    <View >
                        <Text>Technologies</Text>
                    </View>

                    <View style={styles.details}>
                        <Text>
                            {FrameworkFilter.map((item) => {

                                return(
                                    <Text>{item.TechnoName} | </Text>
                                )
                              
                            })}
                        </Text>
                        <Text>
                            {LanguageFilter.map((item) => {

                                return(
                                    <Text>{item.TechnoName} | </Text>
                                )
                              
                            })}
                        </Text>
                    </View>
                </View>
            </View>

            <View style={styles.section}>
                <View >
                    <Text style={styles.titleSection} >Experiences</Text>
                </View>
                <View style= {styles.contentSection}>
                    {experienceData.map(({ company, begindate, enddate, role, details }) => (
                    <View  style={styles.titleSection}>
                            <Text>{company} | {begindate} - {enddate} -- {role}</Text>
                        <View style={styles.details}>
                            <Text>{details}</Text>
                        </View>
                    </View>
                    ))}
                </View>
            </View>
            <View style={styles.section}>
                <View >
                    <Text style={styles.titleSection} >Education</Text>
                </View>
                <View className={styles.display}>
                <View style={styles.contentSection}>    
                    {certificationsData.map(({company, begindate, enddate, role, description }) => (
                        <View  style={styles.titleSection}>
                            <Text>{company} | {begindate} - {enddate} -- {role}</Text>
                        <View style={styles.details}>
                            <Text>{description}</Text>
                        </View>
                        </View>
                        ))}
                </View>
                <View style={styles.contentSection}>
                    {educationData.map(({company, begindate, enddate, role, description }) => (
                        <View  style={styles.titleSection}>
                            <Text>{company} | {begindate} - {enddate} -- {role}</Text>
                        <View style={styles.details}>
                            <Text>{description}</Text>
                        </View>
                        </View>
                        ))}
                </View>
                </View>
            </View>



            <View style={styles.footer}>

                <Link href="mailto:leo.gravelin@protonmail.com" style={styles.link}>
                        Contact : leo.gravelin@protonmail.com
                </Link>

            </View>
        

        
        
        </View>
        </Page>
      </Document>
     
    );
};
export default MyDoc;


