import React, { useState, useEffect } from 'react';
import ProgressBar from './ProgressBar';
import  { csv } from 'd3';
import {CSVurl} from "../../config"
const Languages = () => {
    const [data, setData] = useState([]);
    useEffect(()=> {
        csv(CSVurl).then(data=>{
        setData(data)
        });
    
    }, []);
    
    const LanguageFilter = data.filter(x => x.TechnoType === "Language" && x.Open_Skill === "Yes")
    const FrameworkFilter = data.filter(y => y.TechnoType === ("Framework" || "Engine" || "Library" || "Tool") && y.Open_Skill === "Yes")
    
    return (
        <div className="ProgressBarDisplay">
            <ProgressBar
                data = {LanguageFilter}
                className = "LanguageDisplay"
                title = "Languages" 
            />

            <ProgressBar
                data = {FrameworkFilter}
                className = "LanguageDisplay"
                title = "Framework, Libraries & Engines..." 
            />
            
        </div>
    );
}

export default Languages;
