
import React from 'react';


const ProgressBar = (props) => {

    return (
        <div className={props.className}>
            <h4>{props.title}</h4>
            <div className="years">
                <span>Expérience en mois</span>
                <span>6 mois</span>
                <span>12 mois</span>
                <span>18 mois</span>

            </div>
            <div>
                {
                    props.data.map((item) =>{

                        let xpYears = 18;
                        let progressBar = item.Training_Month / xpYears *100 + '%';
                        return(
                            <div key={item.Id} className="languageList">
                                <ul>
                                    <li>{item.TechnoName}</li>
                                </ul>
                                <div 
                                    className="progressBar" 
                                    style={{width:progressBar}}>
                                </div>

                            </div>
                            
                        )
                      
                    
                    })
                }
            </div>

        </div>

      
    );
};

export default ProgressBar;