import React from 'react';
import SocialLinks from './atomes/SocialLinks';



function getCurrentDate(){

    let newDate = new Date()
    let year = newDate.getFullYear();
    
    return `${year}`
    }



const Contact = () => {
    return (
        <div className="contact-footer">


            <div>
                <a 
                href="mailto:leo.gravelin@protonmail.com" rel="noopener noreferrer">leo.gravelin@protonmail.com</a>
            </div>
            
            <div>
                <SocialLinks></SocialLinks>
              
            </div>
    

            <div id="copyright">
            <p>copyright <span>{getCurrentDate()}</span> &copy; L.Gravelin</p>
            </div>
                        
        </div>
    );
};

export default Contact;




