import React from 'react';

import MyDoc from './toPDF';


import { PDFDownloadLink } from '@react-pdf/renderer';



import Availablepoint from './Availablepoint';

// constante import
import imageData from './cv/Data/DataPicture';
import baselineData from './cv/Data/DataBaseline';
import { NavLink } from 'react-router-dom';

const Identity = () => {
    return (


            <div className="elementFlex flexContainer" id="myHeader">

            <div replace  to="" className="identityCorner">
                <NavLink to="" replace>
                <div className="gxxQmM">
                    <img src={imageData[0]} alt="myPicture" id="id-picture" className="kpMpiu elementFlex bUfKwZ"/>
                </div>

                <div className="liicIq">
                    <h1 className="lxhMO">Léo Gravelin</h1>
                </div>

                <h2 class="jCAZjK">{baselineData[0]}</h2>

                </NavLink>

                <div className="fSuEpE elementFlex">

                    <div className="available">
                        <Availablepoint
                            className = ""
                        ></Availablepoint>
                    </div>

                    <div className="Cokob elementFlex">
                       
                     
                        <div 
                        class="dlLzLI">
                            <a
                                title="Download PDF" 
                                href="Download">

                                <span> <PDFDownloadLink document={<MyDoc/>} fileName="Leo_Gravelin_CV.pdf">
                                        {({ blob, url, loading, error }) =>
                                            loading ? 'Loading document...' : 'Télécharger CV.pdf'
                                        }
                                        </PDFDownloadLink>
                                </span>
                            </a>
                        </div>

                    </div>

                </div>
            </div>


            <div className="contentCorner">
                <p>{baselineData[1]}</p>
                

            </div>
    



            </div>
            

            
      
    );
};

export default Identity;